/*photo upload container*/
.photo-upload {
  position: relative;
  text-align: center;

  .drag-n-drop {
    @include hide;
  }

  .capture {
    @include hide;
  }

  &.mobile {
    .select {
      margin-bottom: 15px !important;
      width: 100%;
    }

    .capture {
      @include show;
      margin-bottom: 15px !important;
      width: 100%;
    }
  }

  /*When dragndrop is supported*/
  &.advanced-upload {
    background: lighten($main-background-color, 5);
    outline: 2px dashed lighten($bootstrap-secondary, 10);
    outline-offset: -10px;

    &.is-dragover {
      @include ease-in;
      outline-offset: -20px;
      outline-color: lighten($bootstrap-secondary, 35);
      background: lighten($main-background-color, 25);
    }

    /* Drag-n-drop indicator on modern devices*/
    .drag-n-drop {
      @include show;

      .icon {
        @include fluid-type($screen-xs, $screen-xl, 70px, 100px);
        text-align: center;
        font-size: 70px;
        color: $bootstrap-secondary;
        margin: 0 auto;
        padding: 30px 10px;
      }

      .info {
        color: $bootstrap-secondary;
        .title {
          @include fluid-type($screen-xs, $screen-xl, 20px, 28px);
        }
      }
    }
  }

  .avatar-input:disabled ~ .upload-photo-label,
  .avatar-input:disabled ~ .upload-photo-label:hover,
  .avatar-input:disabled ~ .upload-photo-label:focus,
  .avatar-input:disabled ~ .upload-photo-label:active {
    background: #fff;
    color: $disabled-gray-button;
    border-color: $disabled-gray-button;
  }
}

/*upload progress indicator*/
.upload-progress {
  .progress-bar {
    @include ease-out;
  }
}

.upload-photo-label {
  vertical-align: middle;
  i {
    display: inline-block;
    font-size: 32px;
    padding-right: 4px;
    padding-left: 5px;
  }
}

.avatar-requirements {
  ul {
    list-style: none;

    li {
      font-weight: 300;
      font-size: $paragraph-size;
    }
  }
}
